import React from 'react';
import { makeStyles,  } from '@material-ui/core/styles';
import { Props as ApplicationProps } from '../index';
import { iApc } from './index';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid';

import IconButton from '@material-ui/core/IconButton';
import CalendarTodayIcon from '@material-ui/icons/CalendarToday';
import EventBusyIcon from '@material-ui/icons/EventBusy';
import DateRangeIcon from '@material-ui/icons/DateRange';
import LocalAtmIcon from '@material-ui/icons/LocalAtm';



const useStyles = makeStyles((theme) => ({
  table: {
    minWidth: 100,
  },
  root: {
    flexGrow: 1
  },
  paper: {
    padding: theme.spacing(2),
    margin: "auto",
    maxWidth: 500
  },
  bottomRight: {
    justifyContent: "flex-end"
  },
  outerColumn: {
    borderRight: "1px solid grey",
    borderBottom: "1px solid grey",
    borderLeft: "1px solid grey"
  },
  centerColumn: {
    borderBottom: "1px solid grey"
  },
  margin: {
    margin: theme.spacing(1),
  },
}));

interface Props extends ApplicationProps {
  refApc: iApc[]
}

function Form(props: Props) {
  const classes = useStyles();
  const { application: { borrower }, setApplication, refApc } = props;

  let debt_capacity=0, debt_capacity_hip=0, discount_capacity_hip=0, salario=0, aRecibir=0, hipoteca_sn=false
  const dataApp = window.localStorage.getItem('quotation');
  if (dataApp) {
    const Quotation = JSON.parse(dataApp)
    aRecibir=Quotation.loan.amount
    debt_capacity=Quotation.loan.debt_capacity
    debt_capacity_hip=Quotation.loan.debt_capacity_mortgage
    discount_capacity_hip=Quotation.loan.discount_capacity_mortgage
    salario=Quotation.borrower.wage +( Quotation.borrower.alloance || 0) + (Quotation.borrower.perDiem || 0)
    // console.log(debt_capacity, debt_capacity_hip, discount_capacity_hip, salario)

    // 1	Casa Propia
    // 2	Padres o Familiares
    // 3	Casa Hipotecada
    // 4	Casa Alquilada

    hipoteca_sn = Quotation.borrower.residenceType === "3"
  }
  //***************************************/
  //***************************************/
  // Datos para Pruebas de Calculo
  aRecibir = 20000
  discount_capacity_hip = 20
  debt_capacity = 45
  debt_capacity_hip = 45
  salario = 2000
  //***************************************/
  //***************************************/

  const montoLoan = aRecibir
  
  refApc.sort(function (a, b) {
    if (Number(a.saldoActual) < Number(b.saldoActual)) {
      return 1;
    }
    if (Number(a.saldoActual) > Number(b.saldoActual)) {
      return -1;
    }
    // a must be equal to b
    return 0;
  });

  const fp0 = refApc.filter(f => f.tipoObligacion !== "SERVICIOS")
  for(let deuda of fp0) {
    if(deuda.tipoObligacion === "L. ROTATIVA" || deuda.tipoObligacion === "TMA")
      deuda.letra = deuda.saldoActual * 0.05
    
    if(deuda.tipoObligacion === "TARJ. CREDITO")
      deuda.letra = deuda.saldoActual * 0.03

    deuda.montoAplicado = 0.00
    deuda.montoARecibir = 0.00

  }
  const fp1 = fp0.filter(f => f.formaPago === "DESCUENTO DIRECTO")
  const fp2 = fp0.filter(f => f.formaPago !== "DESCUENTO DIRECTO")
  const fp = fp1.concat(fp2)

  let saldoDD = 0
  for(let ref of fp) {
    const { saldoActual, tipoObligacion, formaPago } = ref
    if ( saldoActual <= aRecibir &&  tipoObligacion !== 'HIPOTECA') {
      aRecibir -= saldoActual
      ref.montoAplicado = saldoActual
      ref.montoARecibir = aRecibir
      if(formaPago === "DESCUENTO DIRECTO")
        saldoDD += Number(ref.saldoActual)
    }
    // if (['PREST. PERSONAL','PREST. AUTO','TARJ. CREDITO'].includes(ref.tipoObligacion)) {
    //   saldoDD += Number(ref.saldoActual)
    // }
  }

  // for(let ref of fp2) {
  //   const { saldoActual, tipoObligacion } = ref
  //   if ( saldoActual <= aRecibir &&  tipoObligacion !== 'HIPOTECA') {
  //     aRecibir -= saldoActual
  //     ref.montoAplicado = saldoActual
  //     ref.montoARecibir = aRecibir
  //     console.log(ref.montoARecibir)
  //   }
  // }
  
  const fpp = fp.filter(f => f.montoAplicado > 0)

  const handleOnClick = () => {
    let contom = 1
    for(let ref of fp0) {
      const { contometro } = ref
      for(let l of contometro) {
        if (l > "3") {
          console.log(ref);
          contom = 0
          break
        }
      }
      if (contom === 0) break

    }  

    const capDescto = salario * discount_capacity_hip/100
    const capEndeud = salario * (hipoteca_sn ? debt_capacity_hip : debt_capacity)/100

    let letrasP = 0, letrasT = 0
    for(let ref of fp0) {
      if (ref.saldoActual !== ref.montoAplicado) {
        letrasP += Number(ref.letra)
      }
      letrasT += Number(ref.letra)
    }

    const pjeCapEndDespPrest = (letrasP + capDescto)/salario*100
    const pjeCapEndAntePrest = (letrasT + capDescto)/salario*100
    const endeudamiento = pjeCapEndDespPrest > (hipoteca_sn ? debt_capacity_hip : debt_capacity) ? 0 : 1
    const saldoPorDD = saldoDD > montoLoan ? 0 : 1

    // console.log((letrasP + capDescto)/salario*100)
    // console.log(hipoteca_sn ? debt_capacity_hip : debt_capacity)

    const data = {
      '00- Monto Préstamo': montoLoan,
      '01- Monto a Recibir': aRecibir,
      '02- % Capacidad Descto. Hip': discount_capacity_hip,
      '03- % Capacidad Endeudam. Hip': debt_capacity_hip,
      '04- % Capacidad Endeudam.': debt_capacity, 
      '05- Salario': salario,
      '06- Capacidad Descto.': capDescto, 
      '07- Capacidad Endeudam.': capEndeud, 
      '08- Letra Mensual': letrasP, 
      '09- Tiene Hip.': hipoteca_sn,
      '10-': "=============================" ,
      '11- Contometro': contom, 
      '12- Endeudamiento': endeudamiento,
      '13- Saldos por DD': saldoPorDD,
      '14-': "=============================",
      '15- % Cap. Endeud. despues Prest.': pjeCapEndDespPrest,
      '16- % Cap. Endeud. antes Prest.': pjeCapEndAntePrest,
      '17- Saldos Descto. Directo': saldoDD,
    }
    console.log(data);

    setApplication({
      borrower: {
        contometro: contom, 
        endeudamiento,
        saldoPorDD,
        pjeCapEndeud: pjeCapEndDespPrest ,
        saldoDD
      }
    })

    const xx = document.getElementById('aplicar');
    if (xx)
      xx.setAttribute('style', 'display: none')
  }

  // const style = {
  //   width: '50%', 
  //   margin: '10px',
  //   padding: '0.8em',
  //   fontSize: '1.3em',
  //   border: '1px solid #bdb8b8',
  //   borderRadius: '10px'
  // }

  // const style_2 = {
  //   width: '40%', 
  //   margin: '0 auto'
  // }

  const styleButton = {
    padding: '10px 15px 10px 15px',
    border: 'none',
    borderRadius: '25px',
    background: '#0C7BDD',
    color: 'white'
  }

  return (
    <>
      {/* <div style={style}>
        Monto del Préstamo: {new Intl.NumberFormat("en-US", {style: 'currency', currency: 'USD', minimumFractionDigits: 2}).format(montoLoan)}
      </div> */}
      <Typography variant="h4" align="center">Monto Solicitado: {new Intl.NumberFormat("en-US", {style: 'currency', currency: 'USD', minimumFractionDigits: 2}).format(montoLoan)}</Typography>
      <Typography variant="h6">Obligaciones Activas Actuales</Typography>
      <TableContainer component={Paper}>
        <Table className={classes.table} size="small" aria-label="a dense table" style={{border: '3px solid gray'}}>
          <TableHead>
            <TableRow>
              <TableCell>Institución</TableCell>
              <TableCell>Obligación</TableCell>
              <TableCell>F.Pago</TableCell>
              <TableCell align="right">Saldo</TableCell>
              <TableCell align="right">Letra</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {fp.map((row) => (
              <TableRow key={row.referencia}>
                <TableCell component="th" scope="row">
                  {row.institucion}
                </TableCell>
                <TableCell>{row.tipoObligacion}</TableCell>
                <TableCell>{row.formaPago}</TableCell>
                <TableCell align="right">{new Intl.NumberFormat("en-US", {currency: 'USD', minimumFractionDigits: 2}).format(row.saldoActual)}</TableCell>
                <TableCell align="right">{new Intl.NumberFormat("en-US", {currency: 'USD', minimumFractionDigits: 2}).format(row.letra)}</TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
      <div id="aplicar">
      <br />
      <button style={styleButton} onClick={handleOnClick}>Aplicar pago Obligaciones</button>
      <br />
      </div>
      {borrower.contometro !== undefined ? (
        <>
          {(borrower.contometro + (borrower.saldoPorDD === undefined ? -1: borrower.saldoPorDD)  + (borrower.endeudamiento === undefined ? -1: borrower.endeudamiento) < 3) ? (
            <>
              <Typography variant="h6">Lamentablemente usted no califica debido a los siguientes criterios</Typography>
              {/* <div style={style_2}> */}

              <TableContainer>
                  <Table className={classes.table} size="small" aria-label="a dense table" style={{border: '3px solid gray', width: '80%'}}>
                    <TableHead>
                      <TableRow>
                        <TableCell align="center" style={{fontSize: '20px'}}>Criterio</TableCell>
                        <TableCell align="center" style={{borderLeft: '3px solid gray', fontSize: '20px'}}>Comentario</TableCell>
                      </TableRow>
                    </TableHead>
                    <TableBody>
                      {!borrower.contometro ? (
                        <TableRow>
                          <TableCell>
                            <IconButton color="primary" className={classes.margin}>
                              <CalendarTodayIcon fontSize="large"/>
                              <Typography>&nbsp;&nbsp;Días de Atraso</Typography>
                            </IconButton>
                          </TableCell>
                          <TableCell style={{borderLeft: '3px solid gray'}} component="th" scope="row">En 24 meses a mantenido atrasos &gt; 60 días</TableCell>
                        </TableRow>
                      ):""}
                      {!borrower.saldoPorDD ? (
                        <TableRow>
                          <TableCell>
                            <IconButton color="primary" className={classes.margin}>
                              <EventBusyIcon fontSize="large"/>
                              <Typography>&nbsp;&nbsp;Nivel Endeudamiento</Typography>
                            </IconButton>
                          </TableCell>
                          <TableCell style={{borderLeft: '3px solid gray'}} component="th" scope="row">45% ( se excede de los requerido )</TableCell>
                        </TableRow>
                        ):""}
                        {!borrower.endeudamiento ? (
                        <TableRow>
                          <TableCell>
                            <IconButton color="primary" className={classes.margin}>
                              <DateRangeIcon fontSize="large"/>
                              <Typography>&nbsp;&nbsp;Total Obligaciones por Descuento</Typography>
                            </IconButton>
                          </TableCell>
                          <TableCell style={{borderLeft: '3px solid gray'}} component="th" scope="row">Se excede de los solicitadp</TableCell>                     
                        </TableRow>
                        ):""}
                        {!borrower.contometro ? (
                        <TableRow>
                          <TableCell>
                            <IconButton color="primary" className={classes.margin}>
                              <LocalAtmIcon fontSize="large"/>
                              <Typography>&nbsp;&nbsp;Score APC</Typography>
                            </IconButton>
                          </TableCell>
                          <TableCell style={{borderLeft: '3px solid gray'}} component="th" scope="row">50%</TableCell>
                        </TableRow>
                        ):""}
                    </TableBody>
                  </Table>
                </TableContainer>       
            </>
          ) : 
          (
            <>
              <Typography variant="h6">Obligaciones a Cancelar por Descuento</Typography>
              {/* <TableContainer component={Paper}> */}
              <Table className={classes.table} size="small" aria-label="a dense table" style={{border: '3px solid gray', width: '80%'}}>
                <TableHead>
                  <TableRow>
                    <TableCell>Institución</TableCell>
                    <TableCell>Obligación</TableCell>
                    <TableCell align="right">Saldo</TableCell>
                    <TableCell align="right">Letra</TableCell>
                  </TableRow>
                </TableHead>
                <TableBody>
                  {fpp.map((row) => (
                    <TableRow key={row.referencia}>
                      <TableCell component="th" scope="row">
                        {row.institucion}
                      </TableCell>
                      <TableCell>{row.tipoObligacion}</TableCell>
                      <TableCell align="right">{new Intl.NumberFormat("en-US", {currency: 'USD', minimumFractionDigits: 2}).format(row.saldoActual)}</TableCell>
                      <TableCell align="right">{new Intl.NumberFormat("en-US", {currency: 'USD', minimumFractionDigits: 2}).format(row.montoARecibir)}</TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
              {/* </TableContainer>       */}
              <Typography variant="h6">con el cual, usted recibirá en mano despues de dichas cancelaciones</Typography>

              {/* <div style={style}>
                Monto a Recibir: {new Intl.NumberFormat("en-US", {style: 'currency', currency: 'USD', minimumFractionDigits: 2}).format(aRecibir)}
              </div> */}
              <Typography variant="h4">Monto a Recibir: {new Intl.NumberFormat("en-US", {style: 'currency', currency: 'USD', minimumFractionDigits: 2}).format(aRecibir)}</Typography>
            </>
          )}
        </>
      )
      : ""}
    </>
  );
}

export default Form;