import React from 'react'
import { makeStyles, useTheme } from '@material-ui/core/styles';
import { Card, CardContent, Typography, Input, IconButton } from '@material-ui/core'
import useMediaQuery from '@material-ui/core/useMediaQuery';
import PublishIcon from '@material-ui/icons/Publish';
import Grid from '@material-ui/core/Grid';
import CameraAltOutlinedIcon from '@material-ui/icons/CameraAltOutlined';

const useStyles = makeStyles((theme) => ({
  root: {
    width: '100%',
    height: '100%',
    padding: '24',
    textAlign: "center"
  },
  button: {
    width: 58,
    height: 58,
    color: "#0C7BDD"
  },
  texto: {
    fontSize: "0.8rem"
  }
}));

const API_HOST = ''; //process.env.REACT_APP_API_HOST;

function CardDocument(props) {
  const theme = useTheme();
  const desktop = useMediaQuery(theme.breakpoints.up('sm'));
  const classes = useStyles();
  const { option, propsA } = props;
  const { application: { borrower, documents }, setApplication } = propsA;

  let typeDoc = ''
  let bank = ''
  let propsQ = ''
  const data = window.localStorage.getItem('quotation');
  if (data) {
    propsQ = JSON.parse(data)
    bank = propsQ.loan.bank
  }

  const takeFile = async (e) => {
    const { id } = borrower;

    if (e.target.files.length > 0) {
      const fileUpload = e.target.files[0]
      typeDoc = e.target.id

      const  formData = new FormData()
      formData.append("idUrl", fileUpload)
      formData.append("entity_f", bank)
      formData.append("prospect", id)
      formData.append("nameImage", typeDoc)

      try {
        const uploadFileRes = await fetch(`${API_HOST}/upload/file`, {
            method: 'POST',
            body: formData
        })

        const uploadFileData = await uploadFileRes.json()
        console.log(uploadFileData)
        updateDocuments()
      } catch (e) {
        console.error(e);
      }
    }
  }

  function updateDocuments() {
    switch (typeDoc) {
      case 'CEDULA':
        documents.idUrl = typeDoc
      break;
      case 'COMP-PAGO':
        documents.payStubUrl = typeDoc
      break;
      case 'FICHA-SS':
        documents.socialSecurityProofUrl = typeDoc
      break;
      case 'SERV-PUBLICO':
        documents.publicGoodProofUrl = typeDoc
      break;
      case 'CARTA-TRABAJO':
        documents.workLetterUrl = typeDoc
      break;
      default:

    }
    setApplication({ documents: documents })
  }

  return (
    <Grid item xs={6} sm={6} md={2}>
    <Card className={classes.root}>
      <CardContent className={classes.root}>
        <label htmlFor="raised-button-file">
          <IconButton variant="raised" component="label">
            {desktop ? <PublishIcon className={classes.button}/> : <CameraAltOutlinedIcon  className={classes.button}/>}
            <Input id={option.id_name} type="file" style={{display: 'none'}} accept="image/png, image/jpeg, application/pdf" onChange={takeFile}/>
          </IconButton>
        </label> 
        <Typography className={classes.texto}>{option.name.toUpperCase()}</Typography>
      </CardContent>
    </Card>
    </Grid>
  )
}

export default CardDocument;