import React from 'react';
import Grid from '@material-ui/core/Grid';
import { Props as ApplicationProps } from '../index';
import { Document } from './index';
import CardDocument from './CardDocument';

interface Props extends ApplicationProps {
  documentos: Document[]
}

function Form(props: Props) {
  const { application: { documents }, documentos } = props;

  documentos.map((doc, idx) => {
    if(doc.id_name==='CEDULA') {
      if(documents.idUrl !== undefined) documentos.splice(idx,1)
    }
    if(doc.id_name==='FICHA-SS') {
      if(documents.socialSecurityProofUrl !== undefined) documentos.splice(idx,1)
    }
    if(doc.id_name==='FICHA-SS') {
      if(documents.socialSecurityProofUrl !== undefined) documentos.splice(idx,1)
    }
    if(doc.id_name==='SERV-PUBLICO') {
      if(documents.publicGoodProofUrl !== undefined) documentos.splice(idx,1)
    }
    if(doc.id_name==='COMP-PAGO') {
      if(documents.payStubUrl !== undefined) documentos.splice(idx,1)
    }
    if(doc.id_name==='CARTA-TRABAJO') {
      if(documents.workLetterUrl !== undefined) documentos.splice(idx,1)
    }
  })

  return (
    <Grid container spacing={3}>
        {documentos.map((item, idx) => (
          <CardDocument key={idx} option={item} propsA={props} />
        ))}
      </Grid>
  );
}

export default Form;